<template>
  <div class="container-fluid">
    <router-link to="/" class="position-relative position-absolute top-0 start-0">
            <button class="back-btn btn-color-1" @click="backbutton" style="display: inline;"><ion-icon name="arrow-back-outline"></ion-icon></button>
    </router-link>
    <div class="app-container">
        <div class="title">
          <h1>Todo App</h1>
        </div>
        <div class="actions">
          <form @submit.prevent="addTask">
            <div class="input-grid">
                <input type="text" class="search-grid" placeholder="Search Task" v-model="searchQuery">
                <input type="text" class="add-grid" placeholder="Add Task" v-model="task" required>
                <button type="submit" class="btn-grid">Add</button>
              </div>
          </form>
        </div>
      <div class="tasks">
        <div
          class="task-items"
          v-for="todo in filteredTask"
          :key="todo.id"
          @click="doneTask(todo.id)"
        >
          <p :class="{ done: todo.status }">{{ todo.details }}</p>
          <button class="done-btn btn-color-1">Done</button>
          <button class="remove-btn btn-color-2" @click="removeTask(todo.id)">Remove</button>
        </div>
        <div class="footerbut">
          <button class="clear-btn" @click="clearTask" style="display: inline;">Clear All Tasks</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { v4 as uuidv4 } from 'uuid';

export default {
  data() {
    return {
      task: '',
      searchQuery: '',
      todos: [],
    };
  },
  methods: {
    addTask() {
      if (!this.task.trim()) {
        return;
      }

      const taskExists = this.todos.some((task) => task.details === this.task);

      if (taskExists) {
        alert('Task already exists');
      } else {
        const newTask = {
          id: uuidv4(),
          details: this.task,
          status: false,
        };
        this.todos.unshift(newTask);
        this.task = '';
      }
    },
    removeTask(id) {
      const index = this.todos.findIndex((todo) => todo.id === id);

      if (index !== -1) {
        const confirmed = window.confirm('Are you sure you want to remove this task?');

        if (confirmed) {
          this.todos.splice(index, 1);
        }
      }
    },
    doneTask(id) {
      const done = this.todos.find((todo) => todo.id === id);
      if (done) {
        done.status = !done.status;
      }
    },
    clearTask() {
      const confirmed = window.confirm('Are you sure you want to clear all tasks?');

      if (confirmed) {
        this.todos = [];
      }
    },
  },
  computed: {
    allTask() {
      return this.todos.length > 0;
    },
    filteredTask() {
      return this.todos.filter((todo) => {
        return todo.details.toLowerCase().includes(this.searchQuery.toLowerCase());
      });
    },
  },
};
</script>

<style scoped>
  .app-container{
    width: 50%;
    min-width: 300px;
    margin: auto;
  }

  .title {
    text-align: center;
    font-size: 28px;
    margin: 50px 0 50px 0;
  }

  .actions form {
    display: flex;
    justify-content: space-between;
  }

  .actions input[type="text"] {
    flex: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }

  .actions button[type="submit"] {
    background-color: #33373a;
    color: #fff;
    border: 0;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
  }

  .input-grid {
    width: 100%;
    column-gap: 5px;
    row-gap: 5px;
    display: grid;
    grid-template-rows: 50px 50px;
    grid-template-columns: repeat(4, 1fr);
  }
  .search-grid{
    grid-row: 1 / 1;
    grid-column: 1 / 5;
  }
  .add-grid{
    grid-row: 2 / 2;
    grid-column: 1 / 4;
  }
  .btn-grid{
    grid-row: 3 / 2;
    grid-column: 4 / 5;
  }

  .tasks .task-items {
    display: flex;
    align-items: center;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin: 10px 0;
    cursor: pointer;
    transition: background-color 0.3s;
  }

  .tasks .task-items:hover {
    background-color: #f2f2f2;
  }

  .tasks p {
    flex: 1;
    margin: 0;
    padding: 10px;
    font-size: 18px;
  }

  .tasks .done-btn,
  .tasks .remove-btn {
    background-color: rgb(0, 0, 0);
    color: #fff;
    border: 0;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
  }

  .back-btn {
    background-color: rgb(0, 0, 0);
    color: #fff;
    border: 0;
    /* border-radius: ; */
    padding: 10px 15px;
    cursor: pointer;
  }

  .tasks .done-btn {
    background-color: #fafafa;
    color: black;
  }

  .clear-btn {
    background-color: #333;
    color: #fff;
    border: 0;
    padding: 10px 20px;
    cursor: pointer;
    margin-block-end: auto;
    margin-top: 20px;
    font-weight: 600;
    transition: all 300ms ease;
    width: 10rem;
    float: right;
    border-radius: 5px;
  }

  .done {
    text-decoration: line-through;
  }


  .btn-color-1,
.btn-color-2 {
  border: rgb(53, 53, 53) 0.1rem solid;
}

.btn-color-1:hover,
.btn-color-2:hover {
  cursor: pointer;
  color: white;
}

.btn-color-1,
.btn-color-2:hover {
  background: rgb(53, 53, 53);
  color: white;
}

.btn-color-1:hover {
  background: rgb(0, 0, 0);
}

.btn-color-2 {
  background: none;
}

.btn-color-2:hover {
  border: rgb(255, 255, 255) 0.1rem solid;
}

.btn-color-1:hover {
  background: rgb(0, 0, 0);
}

</style>
