<template>
  <div class="game-container">
    <router-link to="/" class="pos-btn">
            <button class="back-btn" @click="backbutton" style="display: inline;"><ion-icon name="arrow-back-outline"></ion-icon></button>
    </router-link>
    <div class="memory-game">
      <div class="title">Memory Game</div>
      <div class="memory-board">
        <div
        v-for="card in cards"
        :key="card.id"
        :class="['memory-card', { flipped: card.flipped }]"
        @click="flipCard(card)"
        >
        <div class="card-back">?</div>
        <div class="card-front" :style="{ backgroundColor: card.color }"></div>
      </div>
      </div>
      <div class="container">
        <button @click="resetGame" class="reset-button">Reset Game</button> <!-- Add this line -->
      </div>
    </div>
  </div>
  </template>
 
 <script>
  import _ from 'lodash';
 
 export default {
 data() {
     return {
       cards: [],
       chosenCards: [],
       lockCards: false,
     };
 },
 created() {
     this.createCards();
     this.shuffleCards();
 },
 methods: {
     createCards() {
       const cardColors = [
         '#FFC0CB',
         '#0000FF',
         '#8B4513',
         '#FFD700',
         '#808080',
         '#4B0082',
         '#FF00FF',
         '#9932CC',
       ];
 
       this.cards = [];
       for (let i = 0; i < 8; i++) {
         this.cards.push({ id: i, name: 'card' + i, color: cardColors[i], flipped: false });
         this.cards.push({ id: i + 8, name: 'card' + i, color: cardColors[i], flipped: false });
       }
     },
     shuffleCards() {
       this.cards = _.shuffle(this.cards);
     },
     flipCard(card) {
       if (this.lockCards || card.flipped) {
         return;
       }
       card.flipped = true;
       this.chosenCards.push(card);
       if (this.chosenCards.length === 2) {
         this.lockCards = true;
         setTimeout(() => {
           if (this.chosenCards[0].color !== this.chosenCards[1].color) {
             this.chosenCards.forEach(card => {
               card.flipped = false;
             });
           }
           this.chosenCards = [];
           this.lockCards = false;
         }, 1000);
       }
     },
     resetGame() {
       this.cards.forEach(card => {
         card.flipped = false;
       });
       this.chosenCards = [];
       this.lockCards = false;

       this.shuffleCards();
     },
 },
 };
 </script>
 
 <style scoped>


.game-container {
   display: flex;
   justify-content: center;
   align-items: center;
   height: 100vh;
   margin: 0;
   position: relative;
}

.title{
   display: flex;
   justify-content: center;
   margin-bottom: 20px;
   font-size: 30px;
   font-weight: 600;
}

.memory-game {
  padding: 30px;
 background-color: #f7f7f7;
 
}

.memory-board {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  grid-column-gap: 4px;
  grid-row-gap: 4px;
 box-sizing: border-box;
}

.memory-card {
 width: 5rem;
 height: 5rem;
 box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2);
 cursor: pointer;
 position: relative;
 background-color: #fff;
 overflow: hidden;
 transition: transform 0.5s;
}

.card-back,
.card-front {
 width: 100%;
 height: 100%;
 position: absolute;
 backface-visibility: hidden;
 transition: transform 0.5s;
}

.card-back {
  background-color: #f7f7f7;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  font-weight: 600;
}

.card-front {
 background-color: currentColor;
 transform: rotateY(180deg);
}

.memory-card.flipped .card-front {
 transform: rotateY(0);
}

.container {
   display: flex;
   justify-content: center;
   margin-top: 20px;
}

.reset-button {
   padding: 10px 20px;
   font-size: 18px;
   border: none;
   border-radius: 50px;
   background-color: #181717;
   color: #ffffff;
   cursor: pointer;
   transition: background-color 0.3s;
}

.reset-button:hover {
   background-color: #424242;
   color: #ffffff;
   box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.pos-btn{
  position: absolute;
  top: 0px;
  left: 0px;
  font-size: 18px;
}
.back-btn{
  background-color: rgb(0, 0, 0);
    color: #fff;
    border: 0;
    /* border-radius: ; */
    padding: 10px 15px;
    cursor: pointer;
}
.back-btn:hover{
  background-color: #424242;
   color: #ffffff;
}


</style>